import Vue from 'vue'
import Vuex from 'Vuex'
Vue.use(Vuex) // vuex
let store = new Vuex.Store({ // store
  state: {
    carPanelData: [], // 
    receiveInfo: [
      {
        'name': '',
        'phone': '1381111111',
        'areaCode': '010',
        'landLine': '110000',
        'province': '',
        'cityId': '110100',
        'city': '',
        'countyId': '110106',
        'county': '',
        'add': '6319',
        'default': true
      },
      {
        'name': '',
        'phone': '1381111111',
        'areaCode': '010',
        'landLine': '110000',
        'province': '',
        'cityId': '110100',
        'city': '',
        'countyId': '110106',
        'county': '',
        'add': '6319',
        'default': false
      },
    ],
    maxOff: false, // 
    carShow: false, // 
    carTimer: null // 
  },
  getters: {
    totleCount (state) { // 
      let count = 0 // 
      state.carPanelData.forEach((goods) => { // 
        count += goods.count // 
      })
      return count // 
    },
    totlePrice (state) { // 
      let price = 0 // 
      state.carPanelData.forEach((goods) => { // 
        price += goods.price * goods.count // 
      })
      return price // 
    },
    allChecked (state) { // 
      let allChecked = true // true
      state.carPanelData.forEach((goods) => { // 
        if (!goods.checked) { // 
          allChecked = false // flase
        }
      })
      return allChecked // 
    },
    checkedCount (state) { // 
      let count = 0 // 0
      // eslint-disable-next-line no-undef
      state.carPanelData.forEach((goods) => { // 
        if (goods.checked) { // 
          count += goods.count // 
        }
      })
      return count // 
    },
    checkedPrice (state) { // 
      let price = 0 // 0
      state.carPanelData.forEach((goods) => { // 
        if (goods.checked) { // 
          price += goods.count * goods.price // *
        }
      })
      return price // 
    },
    checkedGoods (state) { // 
      let checkGoods = [] // 
      state.carPanelData.forEach((goods)=>{ // 
        if(goods.checked) // 
        {
          checkGoods.push(goods) // 
        }
      })
      return checkGoods // 
    }
  },
  mutations: {
    addCarPanelData (state, data) { // 
      let bOff = true // bool
      state.carPanelData.forEach((goods) => { // 
        if (goods.sku_id === data.info.sku_id) { // idid
          goods.count += data.count // 
          bOff = false // boolflase
          if (goods.count > goods.limit_num) { // 
            goods.count -= data.count
            state.maxOff = true // true
            return // 
          }
          state.carShow = true // 
        }
      })
      if (bOff) {
        let goodsData = data.info // 
        Vue.set(goodsData, 'count', data.count) // 1
        Vue.set(goodsData, 'checked', true) // 
        state.carPanelData.push(goodsData) // 
        state.carShow = true // 
      }
    },
    delCarPanelData (state, id) { // 
      state.carPanelData.forEach((goods, index) => { // 
        if (goods.sku_id === id) { // id
          state.carPanelData.splice(index, 1) // 
        }
      })
    },
    plusCarPanelData (state, id) { // 
      state.carPanelData.forEach((goods, index) => { // 
        if (goods.sku_id === id) { // id
          if (goods.count >= goods.limit_num) return
          goods.count++ // 
        }
      })
    },
    subCarPanelData (state, id) { // 
      state.carPanelData.forEach((goods, index) => { // 
        if (goods.sku_id === id) { // id
          if (goods.count <= 1) return // 1 
          goods.count-- // -1
        }
      })
    },
    checkGoods (state, id) { // 
      state.carPanelData.forEach((goods, index) => { // 
        if (goods.sku_id === id) { // id
          goods.checked = !goods.checked // 
        }
      })
    },
    allCheckGoods (state, allChecked) { // 
      state.carPanelData.forEach((goods, index) => {
        goods.checked = !allChecked
      })
    },
    delCheckGoods (state) { // 
      let i = state.carPanelData.length // 
      while (i--) // 
      // eslint-disable-next-line brace-style
      {
        if (state.carPanelData[i].checked) // 
        // eslint-disable-next-line brace-style
        {
          state.carPanelData.splice(i, 1)
        }
      }
      state.carPanelData.forEach((goods, index) => { // 
        if (goods.checked) { // 
          state.carPanelData.splice(index, 1) // 
        }
      })
    },
    closePrompt (state) {
      state.maxOff = false // 
    },
    showCar (state) { // 
      clearTimeout(state.carTimer) // 
      state.carShow = true
    },
    hideCar (state) {
      state.carTimer = setTimeout(() => { // 
        state.carShow = false
      }, 500)
    }
  }
})
export default store // 



