<template>
	<div id="main">
		<div class="store-content item">
			<div class="item-box">
					<div class="gallery-wrapper">
						<div class="gallery">
							<div class="thumbnail">
								<ul>
									<!--  -->
									<li :class="{'on':index==imgIndex}" @click="tableImg(index)" v-for="(img,index) in itemInfo.ali_images" :key="index">
										<!-- -->
										<img :src="img" width="54px" height="54px">
									</li>
								</ul>
							</div>
							<div class="thumb">
								<ul>
									<!-- -->
									<li :class="{'on':index==imgIndex}" v-for="(img,index) in itemInfo.ali_images" :key="index">
										<!-- -->
										<img :src="img" width="440px" height="440px"></li>
								</ul>
							</div>
						</div>
					</div>
					<div class="banner">
						<div class="sku-custom-title">
							<div class="params-price">
								<!-- id -->
								<span><em></em><i>{{itemInfo.price}}</i></span>
							</div>
							<div class="params-info">
								<!-- id -->
								<h4>{{itemInfo.title}}</h4>
								<!-- id -->
								<h6>{{itemInfo.sub_title}}</h6>
							</div>
						</div>
						<div class="sku-dynamic-params-panel">
							<div class="sku-dynamic-params clear">
								<span class="params-name"></span>
								<ul class="params-colors">
									<!-- ididid -->
									<li :class="{'cur':color.id==$route.query.itemId}" v-for="(color,index) in itemInfo.sku_list" :key="index">
										<!---->
										<router-link :title="color.color" :to="{name:'Item',query:{itemId:color.id}}">
											<!--  -->
										<img :src="color.image">
										</router-link>
									</li>
								</ul>
							</div>
							<div class="sku-dynamic-params clear">
								<div class="params-name"></div>
								<div class="params-detail clear">
									<div class="item-num js-select-quantity">
										<!-- 1 -->
										<span class="down" :class="{'down-disabled':count<=1}" @click="subCount">-</span>
										<!-- id -->
										<span class="num">{{count}}</span>
										<!--  -->
										<span class="up" :class="{'up-disabled':count>=itemInfo.limit_num}" @click="addCount">+</span>
									</div>
								</div>
							</div>
						</div>
						<div class="sku-status">
							<div class="cart-operation-wrapper clearfix">
								<!-- -->
								<span class="blue-title-btn js-add-cart" @click="addCarPanelHandle"><a></a></span>
							</div>
						</div>
					</div>
				</div>
			</div>
			<prompt></prompt>
		</div>
</template>

<script>
import itemsData from '@/lib/newItemsData'
import prompt from '@/components/prompt'
import axios from 'axios'
export default{
	data(){
		return{
			itemsData,
			//id id
			itemId:this.$route.query.itemId,
			imgIndex:0,// 0
			count:1 // 1
		}
	},
	components:{
		prompt
	},
	watch:{
		'$route.query.itemId'(){ // query
			this.itemId=this.$route.query.itemId
			this.imgIndex=0 // 0
		}
	},
	computed:{
		itemInfo(){
			let itemInfo=this.itemsData.filter((item)=>{ // 
				return Number(item.sku_id)===Number(this.itemId) // idid
			})[0]
			return itemInfo // 
		} 
	},
	methods:{
		tableImg(index){
			this.imgIndex=index; // indexindex
		},
		addCarPanelHandle()
		{
			let itemData={info:this.itemInfo,count:this.count} // 
			this.$store.commit('addCarPanelData',itemData) // 
		},
		addCount(){
			this.count++
			if(this.count>this.itemInfo.limit_num){ // 
				this.count=this.itemInfo.limit_num
			}
		},
		subCount(){
			this.count-- // 
			if(this.count<1){
				this.count=1
			}
		}
	}
}
</script>

<style>
    
</style>


